// Funcin para hacer la peticin de la lista de razas
export async function fetchBreeds() {
  try {
    // Hacer la peticin HTTP a la coleccin de razas
    const response = await axios.get('https://api.thecatapi.com/v1/breeds');
    const breeds = response.data;

    // Devolver las razas
    return breeds;
  } catch (error) {
    // Si hay un error, lnzalo para que pueda ser manejado en el cdigo principal
    throw error;
  }
}

// Funcin para hacer la peticin de informacin sobre un gato por ID de raza
export async function fetchCatByBreed(breedId) {
  try {
    // Hacer la peticin HTTP para obtener informacin sobre el gato
    const response = await axios.get(
      `https://api.thecatapi.com/v1/images/search?breed_ids=${breedId}`
    );
    const catData = response.data[0];

    // Devolver la informacin del gato
    return catData;
  } catch (error) {
    // Si hay un error, lnzalo para que pueda ser manejado en el cdigo principal
    throw error;
  }
}
